}
/* Backgrounds que no se pueden colocar directamente en tailwind */
.background_bienvenida {
  background-image: url("https://www.biogenlinc.mx/content/dam/intl/latam/mexico/mta/hcp/biogenlinc-core/es_MX/lis-app/backgrounds/background_bienvenida.png");
  background-position: center;
}

.background_white {
  background: linear-gradient(
    180deg,
    rgba(255, 255, 255, 0.7) 0%,
    rgba(227, 239, 241, 0) 100%
  );
}
.background_blue {
  background-size: cover;
  background-position: bottom;
  background-repeat: no-repeat;
}
.background_blue_initial {
  background-image: url("https://spacegeneral.sfo2.digitaloceanspaces.com/simuladorlis/blur_initi.1bcac7e3.png");
  /* background: red; */
  background-size: cover;
  background-position: bottom;
  background-repeat: no-repeat;
}
@media screen and (max-width: 320px) {
  .background_blue_colum {
    position: absolute;
    top: calc(28%);
    width: 16rem;
  }
}

@media screen and (min-width: 321px) and (max-width: 373px) {
  .background_blue_colum {
    position: absolute;
    top: calc(32%);
    width: 20rem;
  }
}

@media screen and (min-width: 374px) and (max-height: 667px) {
  .background_blue_colum {
    position: absolute;
    top: calc(35%);
    width: 20rem;
  }
}
@media screen and (min-width: 374px) and (min-height: 668px) {
  .background_blue_colum {
    position: absolute;
    top: calc(30%);
    width: 25rem;
  }
}


